import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import type { Evidence } from '@jan6evidence/types';

import { HighlightContext } from '../../shared/contexts';
import { RAIL_HEIGHT } from '../TimelineHelpers';

const timelineTitle = (evidence: Evidence) => {
  const start = evidence.realTimeStart?.toLocaleTimeString();
  let end = evidence.realTimeEnd?.toLocaleTimeString();
  if (end === start) {
    end = undefined;
  }
  const duration = [start, end].filter(Boolean).join(' - ');
  return `${duration ? `[${duration}] ` : ''}${evidence.summary}`;
};

const Stripe = styled(Link)`
  display: block;
  position: absolute;

  width: 3px;
  height: ${RAIL_HEIGHT}px;

  transform: translate(-50%, 0);
  margin-top: 0;
  z-index: 2;

  cursor: pointer;

  &:hover,
  &.highlighted {
    background-color: white !important;
  }
`;

type Props = {
  evidence: Evidence;
};

const EvidenceStripe = ({ evidence }: Props) => (
  <HighlightContext.Consumer>
    {({ highlight, setHighlight }) => (
      <Stripe
        to={`/evidence/detail/${evidence._id}`}
        title={timelineTitle(evidence)}
        style={{
          left: `${(evidence.timelineStartPercent || 0) * 100}%`,
          backgroundColor: evidence.color,
        }}
        className={highlight === evidence._id ? 'highlighted' : ''}
        onMouseOver={() => setHighlight(evidence._id)}
        onMouseOut={() => setHighlight('')}
      />
    )}
  </HighlightContext.Consumer>
);

export default React.memo(EvidenceStripe);
