import moment from 'moment-timezone';
import React from 'react';

import type { Evidence } from '@jan6evidence/types';

import Tag from '../shared/Tag';

import { colors } from '../shared/constants';

const isJan6Eastern = (date: Date): boolean => {
  const m = moment(date);
  return m.tz('America/New_York').format('YYYY-MM-DD') === '2021-01-06';
};

const toEasternTimeString = (date: Date, forceIncludeDate: boolean = false): string => {
  const m = moment(date);
  const dateStr = m.tz('America/New_York').format('YYYY-MM-DD');
  const timeStr = m.tz('America/New_York').format('h:mm A');
  if (isJan6Eastern(date) && !forceIncludeDate) {
    return timeStr;
  } else {
    return `${dateStr} ${timeStr}`;
  }
};

const toRealTimeFieldStr = (
  realTimeStart: Date | undefined,
  realTimeEnd: Date | undefined
): string => {
  if (!realTimeStart) {
    return '?';
  }
  if (realTimeEnd) {
    const alwaysIncludeDate = !isJan6Eastern(realTimeStart) || !isJan6Eastern(realTimeEnd);
    return `${toEasternTimeString(realTimeStart, alwaysIncludeDate)} - ${toEasternTimeString(
      realTimeEnd,
      alwaysIncludeDate
    )}`;
  } else {
    return toEasternTimeString(realTimeStart);
  }
};

type Props = {
  data: Evidence;
  isActive: boolean;
  activate: () => void;
};

export function ResultThumbnail({ cellData: data }: { cellData: Evidence }) {
  const type = data.videoUrl ? 'video' : 'photo';
  const formattedDuration =
    data.videoStartTimepoint && data.videoEndTimepoint
      ? data.videoEndTimepoint - data.videoStartTimepoint
      : '?';

  return (
    <div className="thumbnail-wrapper">
      <img className={type} src={data.imageUrl} alt="" />
      <span className="type">{type[0].toUpperCase() + type.slice(1)}</span>
      {type === 'video' && (
        <>
          <span className="triangle" />
          <span className="duration">{formattedDuration}</span>
        </>
      )}
    </div>
  );
}

export function ResultSummary({ cellData: data }: { cellData: Evidence }) {
  const summary = data.summary || '';

  return (
    <div className="summary">{`${summary.slice(0, 225)}${summary.length > 255 ? '...' : ''}`}</div>
  );
}

export function ResultTags({ cellData: data }: { cellData: Evidence }) {
  return (
    <div className="tags-wrapper">
      {data.suspectTags &&
        data.suspectTags.map((label) => (
          <Tag
            key={`${data._id}-person-${label}`}
            id={`${data._id}-person-${label}`}
            label={label}
            color={colors.person}
            title={label}
          />
        ))}
      {data.otherTags &&
        data.otherTags.map((label) => (
          <Tag
            key={`${data._id}-other-${label}`}
            id={`${data._id}-other-${label}`}
            label={label}
            title={label}
          />
        ))}
    </div>
  );
}

export function ResultTime({ cellData: data }: { cellData: Evidence }) {
  const timeStr = toRealTimeFieldStr(data.realTimeStart, data.realTimeEnd);

  return <span className="time">{timeStr}</span>;
}

export function ResultLocation({ cellData: data }: { cellData: Evidence }) {
  return <span className="location">{data.locationName || '?'}</span>;
}
