import moment from 'moment-timezone';

export const numberTimeFormatter = (d: number) => moment.tz(d, 'America/New_York').format('h:mm A');

export const timeFormatter = (d: Date | undefined): string | undefined => {
  if (d) {
    return moment(d).tz('America/New_York').format('h:mm A');
  } else {
    return undefined;
  }
};

export const realTimeRangeFormatter = (realTimeStart?: Date, realTimeEnd?: Date): string => {
  const formattedRealTimeStart = timeFormatter(realTimeStart) || '??:??';
  const formattedRealTimeEnd = realTimeEnd ? timeFormatter(realTimeEnd) : undefined;
  if (formattedRealTimeEnd && formattedRealTimeEnd !== formattedRealTimeStart) {
    return `${formattedRealTimeStart} - ${formattedRealTimeEnd}`;
  } else {
    return formattedRealTimeStart;
  }
};

export const videoTimepointFormatter = (timeSec?: number): string | undefined => {
  if (timeSec === undefined) {
    return undefined;
  }
  // https://stackoverflow.com/a/1322771
  // Note that this ignores decimals.
  const hhmmss = new Date(timeSec * 1000).toISOString().substr(11, 8);
  if (hhmmss.startsWith('00')) {
    return hhmmss.slice(1);
  } else {
    return hhmmss;
  }
};

export const videoTimepointRangeFormatter = (
  startSec?: number,
  endSec?: number
): string | undefined => {
  if (startSec === undefined) {
    return undefined;
  }
  const formattedStart = videoTimepointFormatter(startSec) || '??:??';
  const formattedEnd = endSec ? videoTimepointFormatter(endSec) : undefined;
  if (formattedEnd && formattedEnd !== formattedStart) {
    return `${formattedStart} - ${formattedEnd}`;
  } else {
    return formattedStart;
  }
};

export function debounce(f: (...args: any[]) => void, wait: number, immediate?: boolean) {
  let timeout: NodeJS.Timeout | null;
  return (...args: any[]) => {
    const later = () => {
      timeout = null;
      if (!immediate) f(...args);
    };
    const callNow = immediate && !timeout;
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(later, wait);
    if (callNow) f(...args);
  };
}
