import React from 'react';
import { Tweet } from 'react-twitter-widgets';

import { Evidence } from '@jan6evidence/types';

const safeMakeUrl = (urlAsString: string | undefined): URL | undefined => {
  try {
    return new URL(urlAsString || '');
  } catch {
    return undefined;
  }
};

const youtubeIdFromUrl = (urlAsString: string | undefined): string | undefined => {
  const url = safeMakeUrl(urlAsString);
  if (url?.hostname === 'youtu.be') {
    return url?.pathname.slice(1);
  } else if (url?.hostname === 'www.youtube.com') {
    return url?.searchParams.get('v') || undefined;
  } else {
    return undefined;
  }
};

const youtubeIframe = (evidence: Evidence, ytVideoId: string) => {
  const url = new URL(`https://www.youtube.com/embed/${ytVideoId}`);
  if (evidence.videoStartTimepoint) {
    url.searchParams.append('start', evidence.videoStartTimepoint.toString());
  }
  return (
    <iframe
      key={evidence.videoUrl}
      title="embedded evidence video from youtube"
      width="560"
      height="315"
      src={url.href}
      frameBorder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowFullScreen
    />
  );
};

const archiveOrgVideoPathFromUrl = (urlAsString: string | undefined): string | undefined => {
  const url = safeMakeUrl(urlAsString);
  if (url?.hostname === 'archive.org') {
    return url.pathname.split('/').slice(2).join('/') || undefined;
  } else {
    return undefined;
  }
};

const archiveOrgVideoIframe = (evidence: Evidence, archiveOrgVideoPath: string) => {
  const url = new URL(`https://archive.org/embed/${archiveOrgVideoPath}`);
  if (evidence.videoStartTimepoint) {
    url.searchParams.append('start', evidence.videoStartTimepoint.toString());
  }
  return (
    <iframe
      key={evidence.videoUrl}
      title="embedded evidence video from archive.org"
      src={url.href}
      width="640"
      height="480"
      frameBorder="0"
      allowFullScreen
    />
  );
};

const bareMp4VideoTag = (evidence: Evidence) => {
  const url = safeMakeUrl(evidence.videoUrl);
  if (!url) {
    return null;
  }
  if (evidence.videoStartTimepoint && (url.hash || '').trim() === '') {
    url.hash = `t=${evidence.videoStartTimepoint}`;
  }
  return (
    <video controls key={evidence.videoUrl}>
      <source src={url.href} />
    </video>
  );
};

const vimeoIdFromUrl = (urlAsString: string | undefined): string | undefined => {
  const url = safeMakeUrl(urlAsString || '');
  if (url?.hostname?.match(/vimeo\.com$/)) {
    return url.pathname.replace(/^\//, '');
  } else {
    return undefined;
  }
};

const vimeoIframe = (evidence: Evidence, vimeoId: string) => {
  const url = new URL(`https://player.vimeo.com/video/${vimeoId}`);
  if (evidence.videoStartTimepoint) {
    url.hash = `t=${evidence.videoStartTimepoint}s`;
  }
  return (
    <iframe
      key={evidence.videoUrl}
      title="embedded evidence video from vimeo.com"
      src={url.href}
      width="640"
      height="480"
      frameBorder="0"
      allowFullScreen
    />
  );
};

const megaVideoIdFromUrl = (urlAsString: string | undefined): string | undefined => {
  const url = safeMakeUrl(urlAsString || '');
  if (!url || !url.hostname.match(/mega\.nz$/)) {
    return undefined;
  }
  const matchResult = url.href.match(/file\/(.*)/);
  return (matchResult && matchResult[1]) || undefined;
};

const megaVideoIframe = (evidence: Evidence, megaVideoId: string) => {
  let urlString = `https://mega.nz/embed/${megaVideoId}`;
  if (evidence.videoStartTimepoint) {
    urlString += `!${evidence.videoStartTimepoint}s`;
  }
  return (
    <iframe
      key={evidence.videoUrl}
      title="embedded evidence video from mega.nz"
      width="560"
      height="315"
      src={urlString}
      frameBorder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowFullScreen
    />
  );
};

const tweetIdFromUrl = (urlAsString: string | undefined): string | undefined => {
  const url = new URL(urlAsString || '');
  if (!url.hostname.match(/twitter\.com$/)) {
    return undefined;
  }
  const matchResult = url.pathname.match(/status\/(\d+)/);
  return (matchResult && matchResult[1]) || undefined;
};

const embedTweet = (tweetId: string) => {
  return <Tweet tweetId={tweetId} options={{ conversation: 'none' }} />;
};

export const embedVideo = (evidence: Evidence) => {
  if (!evidence.videoUrl) {
    return null;
  }

  const youtubeId = youtubeIdFromUrl(evidence.videoUrl);
  if (youtubeId) {
    return youtubeIframe(evidence, youtubeId);
  }

  const archiveOrgVideoPath = archiveOrgVideoPathFromUrl(evidence.videoUrl);
  if (archiveOrgVideoPath) {
    return archiveOrgVideoIframe(evidence, archiveOrgVideoPath);
  }

  const tweetId = tweetIdFromUrl(evidence.videoUrl);
  if (tweetId) {
    return embedTweet(tweetId);
  }

  const megaVideoId = megaVideoIdFromUrl(evidence.videoUrl);
  if (megaVideoId) {
    return megaVideoIframe(evidence, megaVideoId);
  }

  const vimeoId = vimeoIdFromUrl(evidence.videoUrl);
  if (vimeoId) {
    return vimeoIframe(evidence, vimeoId);
  }

  // default
  return bareMp4VideoTag(evidence);
};

export const embedImage = (evidence: Evidence) => {
  if (!evidence.imageUrl) {
    return null;
  }

  const tweetId = tweetIdFromUrl(evidence.imageUrl);
  if (tweetId) {
    return embedTweet(tweetId);
  }

  return (
    <img
      src={evidence.imageUrl}
      alt={evidence.summary || evidence.locationName || 'Evidence Image'}
    />
  );
};
